import { Layout, Typography } from "antd";
import { useState } from "react";
import FoodList from "./components/FoodList";
import LoginForm from "./components/LoginForm";
import MyCart from "./components/MyCart";
import SignupForm from "./components/SignupForm";

const { Header, Content } = Layout;
const { Title } = Typography;

// Appcomponentindex.js<App />
function App() {
  const [authed, setAuthed] = useState(false);  // check if user is logged in

  return (
    // 100vh (viewport height) - 100%
    // Ant Designheader64px
    // {{ height: "100vh" }}: JS objectbabel
    <Layout style={{ height: "100vh" }}>
      <Header>
        <div>
          <Title
            level={2}
            style={{ color: "white", lineHeight: "inherit", marginBottom: 0 }}
          >
            LaiFood
          </Title>
          <div>{authed ? <MyCart /> : <SignupForm />}</div>
        </div>
      </Header>
      <Content
        style={{
          padding: "50px",
          maxHeight: "calc(100% - 64px)", // csscontent100%64px
          overflowY: "auto",  // Yautoscroll bar
        }}
      >
        {authed ? (
          <FoodList />
        ) : (
          <LoginForm onSuccess={() => setAuthed(true)} />
        )}
      </Content>
    </Layout>
  );
}

export default App;